import { Avatar } from '@mui/material'
import React from 'react'
import { Link } from 'react-router-dom'
import './css/AllQuestion.css'

function AllQuestions() {
  return (
    <div className='all-questions'>
      <div className='all-questions-container'>
        <div className='all-questions-left'>
          <div className='all-options'>
            <div className='all-option'>
              <p>0</p>
              <span>Votes</span>
            </div>
            <div className='all-option'>
              <p>0</p>
              <span>Answers</span>
            </div>
            <div className='all-option'>
              <small>0 Views</small>
            </div>
          </div>
        </div>
        <div className='question-answer'>
          <Link to='/question'>openapi codegen without fromJson and toJson methods?</Link>
          <div style={{
            width: "90%"
          }}>
            <div>I am using openapi-generator-maven-plugin to generate model sources. 
              With my current configuration it generates methods called fromJson and toJson for every model class.
               Is there a way to configure ...</div>
          </div>
        
        <div style={{
          display: "flex"
        }}>
          <span className='question-tags'>java</span>
          <span className='question-tags'>open-api</span>
          <span className='question-tags'>open-api-generator</span>
        </div>
        <div className='author'>
          <small>Timestamp</small>
          <div className='author-details'>
            <Avatar />
            <p>User name</p>
          </div>
          </div>
        </div>

      </div>
    </div>
  )
}

export default AllQuestions
