import {
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  signInWithPopup,
} from "firebase/auth";
import { useNavigate } from "react-router-dom";

// import { useHistory } from "react-router-dom";
import { auth, provider } from "../../firebase";
import React, { useState,useEffect } from 'react';
import './index.css';


function Index() {
  const navigate = useNavigate();
  const [register, setRegister] = useState(false);
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [username, setUsername] = useState("");
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState("")

  const handleSignInGoogle = () => {
    signInWithPopup(auth, provider).then((res) => {
     navigate('/')
     localStorage.setItem('login', true)

    })
    
     
  }
//   useEffect(()=>{
//     let login = localStorage.setItem('login', true);
//     if(!login){
//         navigate('/auth')
//     }
// })
  const handleRegister = (e) => {
    setError("")
    e.preventDefault();
    setLoading(true)
    if (email === "" || password === "" || username === "") {
      setError("Required field is missing")
      setLoading(false)
    } else {
      createUserWithEmailAndPassword(auth, email, password).then((res) => {
        setLoading(false)
        navigate("/");
        
        console.log(res)
      }).catch((error) => {
        console.log(error)
        setError(error.message)
        setLoading(false)
      })
    }
  }
  const handleSignIn = (e) => {
    e.preventDefault();
    setError("")
    setLoading(true)
    if (email === "" || password === "") {
      setError("Required field is missing")
      setLoading(false)
    } else {
      signInWithEmailAndPassword(auth, email, password).then((res) => {
        console.log(res)
        setLoading(false)
       navigate("/");
       
      }).catch((err) => {
        console.log(err.code)
        setError(error.message)
        setLoading(false)
      })
    }
  }
  return (
    <div className='auth'>
      <div className='auth-container'>
        <p>Add another way to log in using any of the following services.</p>
        <div className='sign-options'>
          <div className='single-option' onClick={handleSignInGoogle}>
            <img src="data:image/svg+xml;base64," alt="google" />
            <p>Login with google</p>

          </div>
          {/* <div className="single-option">
            <img
              alt="github"
              src="https://cdn.iconscout.com/icon/free/png-512/github-1521500-1288242.png?f=avif&w=256"
            />
            <p>Login with Github</p>
          </div>
          <div className="single-option">
            <img
              alt="facebook"
              src="https://cdn.iconscout.com/icon/free/png-512/github-1521500-1288242.png?f=avif&w=256"
            />
            <p>Login with Facebook</p>
          </div> */}
        </div>
        <div className='auth-login'>
          <div className='auth-login-container'>
            {
              register ? (<>
                <div className='input-field'>
                  <p>Username</p>
                  <input type="text" value={username} onChange={(e) => setUsername(e.target.value)} />
                </div>
                <div className='input-field'>
                  <p>Email</p>
                  <input type="text" value={email} onChange={(e) => setEmail(e.target.value)} />
                </div>
                <div className='input-field'>
                  <p>Password</p>
                  <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} />
                </div>
                <button onClick={handleRegister} disabled={loading} style={{
                  marginTop: "10px"
                }}>
                  {loading ? "Registering..." : "Register"}</button></>)
                : (<>
                  <div className='input-field'>
                    <p>Email</p>
                    <input type="text" value={email} onChange={(e) => setEmail(e.target.value)} />
                  </div>
                  <div className='input-field'>
                    <p>Password</p>
                    <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} />
                  </div>
                  <button onClick={handleSignIn} disabled={loading} style={{
                    marginTop: "10px"
                  }}>{loading ? "Logging in..." : "Login"}</button></>)
            }
            <p onClick={() => setRegister(!register)} style={{
              marginTop: "10px",
              textAlign: "center",
              color: "#0095ff",
              textDecoration: "underline",
              cursor: "pointer",
            }}>{register ? "Login" : "Register"}?</p>
          </div>
        </div>
        {error !== "" && (
          <p
            style={{
              color: "red",
              fontSize: "14px",
            }}
          >
            {error}
          </p>
        )}
      </div>

    </div>
  )
}

export default Index
